.box:before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.1);
  -webkit-transition: 0.5s;
  transition: 0.5s;
  pointer-events: none;
}

.box:hover:before {
  left: -50%;
  -webkit-transform: skewX(-5deg);
          transform: skewX(-5deg);
}

