.box {
  position: relative;
  /* top: 50%;
  left: 50%; */
  /* transform: translate(-50%, -50%); */
  width: auto;
  height: 300px;
  background: #ffffef;
  box-sizing: border-box;
  overflow: hidden;
  /* box-shadow: 0 20px 50px rgb(23, 32, 90);
  border: 2px solid #2a3cad; */
  color: white;
  padding: 20px;
}

.box:before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.1);
  transition: 0.5s;
  pointer-events: none;
}

.box:hover:before {
  left: -50%;
  transform: skewX(-5deg);
}

.box .content {
  position: relative;
  min-height: 250px;
  padding: 15px;
  text-align: center;
  box-shadow: 0 5px 10px rgba(9, 0, 0, 0.5);
}
.box .content h1 {
  border-bottom: 2px solid #000;
  width: 50%;
  margin: 0 auto;
  padding-bottom: 10px;
}
.box .content h2 {
  color: #908f8f;
}

.box span {
  position: absolute;

  width: 85%;
  height: 100%;
  display: block;
  box-sizing: border-box;
}

.box span:nth-child(1) {
  transform: rotate(0deg);
}

.box span:nth-child(2) {
  transform: rotate(90deg);
}

.box span:nth-child(3) {
  transform: rotate(180deg);
}

.box span:nth-child(4) {
  transform: rotate(270deg);
}

.box1 span:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  background: rgb(255, 190, 7);
  animation: animate 4s linear infinite;
}

.box2 span:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  background: rgb(255, 41, 41);
  animation: animate 4s linear infinite;
}
.box3 span:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  background: rgb(77, 179, 0);
  animation: animate 4s linear infinite;
}
@keyframes animate {
  0% {
    transform: scaleX(0);
    transform-origin: left;
  }
  50% {
    transform: scaleX(1);
    transform-origin: left;
  }
  50.1% {
    transform: scaleX(1);
    transform-origin: right;
  }

  100% {
    transform: scaleX(0);
    transform-origin: right;
  }
}

@keyframes myfirst {
  0% {
    top: 0px;
  }
  25% {
    left: 200px;
    top: 0px;
  }
  50% {
    top: 200px;
  }
  75% {
    top: 200px;
  }
  100% {
    top: 200px;
  }
}
select {
  padding: 10px;
